




* {
  font-family: ' Roboto', sans-serif;
  box-sizing: border-box; }

header {
  position: relative; }

nav {
  height: 50px;
  background-color: #212121; }

.container {
  position: relative;
  width: 1140px;
  margin: 0 auto; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;

  .menu_link {
    color: #ffffff;
    font-size: 15px;
    font-weight: 300; }

  .menu_item {
    border-right: 2px dotted black;
    padding-right: 28px; } }

.subheader {
  position: absolute;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);

  .subheader_logo {
    max-width: 100%; }

  .subheader_official {
    opacity: 0.5;
    color: #ffffff;
    font-size: 13px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal; }

  .subheader_call {
    color: #ffffff;
    font-size: 12px;
    font-weight: 300;
    line-height: 14px;
    margin-top: 19px;
    text-align: right; }

  .subheader_phone {
    color: #ffffff;
    display: block;
    margin-top: 8px;
    font-size: 22px;
    font-weight: 400;
    line-height: 12px;
    text-align: center;
    text-transform: uppercase;
    line-height: 12px; }

  .subheader_btn {
    width: 172px;
    height: 46px;
    margin-top: 19px;
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    text-align: center;
    text-transform: uppercase; } }

.promo {
  min-height: 800px;
  background: url('../img/2-layers.jpg') center (center / cover) no-repeat;
  padding: 150px 0 135px 0;

  .promo_header {
    color: #ffffff;
    font-size: 60px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    line-height: 60px;
    margin: 0; }

  .promo_subheader {
    color: #ffffff;
    font-weight: 700;
    line-height: 36px;
    line-height: 36px;
    text-transform: uppercase;
    margin: 0;
    margin-top: 22px;
    text-align: center; }

  .promo_descr {
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: center;
    margin-top: 23px; }

  .promo_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #ffffff;
    font-size: 18px;
    font-weight: 400;
    text-align: center;
    text-transform: uppercase; } }

.reasons {
  padding: 71px 0 65px 0; }

.label {
  margin: 0 auto;
  height: 24px;
  width: 112px;
  padding: 5px 12px;
  background-color: #000000;
  color: #ffffff;
  font-size: 11px;
  font-weight: 300;
  text-transform: uppercase;
  text-align: center; }

.title {
  margin-bottom: 0;
  margin-top: 38px;
  color: #222222;
  font-size: 38px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center; }

.subtitle {
    color: #222222;
    font-size: 17px;
    font-weight: 400;
    line-height: 21px;
    text-align: center; }

.reasons .reasons_block {
  display: flex;
  justify-content: left;
  align-items: center;
  margin-top: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 35px;

  .reasons_round {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 26px;
    width: 113px;
    height: 113px;
    background-color: #1eacc7;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 26px;
    width: 113px;
    height: 113px;
    background-color: #1eacc7;
    border-radius: 100%; }

  .reasons_descr {
    width: 343px;

    .reasons_subtitle {
      line-height: 21px;
      color: #1eacc7;
      font-size: 17px;
      font-weight: 400; }

    .reasons_text {
      color: #202020;
      font-size: 15px;
      font-weight: 300;
      line-height: 21px; } } }

.row {
  display: flex; }

.column {
  flex: 50%; }

.mobile {
    min-height: 864px;
    padding: 48px 0 79px 0;
    background: url('../img/123.jpg')  center (center / cover) no-repeat;
    .row {
        margin-top: 32px; }
    &_item {
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        padding-top: 91px;
        position: relative;
        &_1 {
            background: url('../img/11.jpg')  center (center / cover) no-repeat; }
        &_2 {
            background: url('../img/2.jpg')  center (center / cover) no-repeat; }
        &_3 {
            background: url('../img/3.jpg')  center (center / cover) no-repeat; }
        &_4 {
            background: url('../img/4.jpg')  center (center / cover) no-repeat; }
        &_5 {
            background: url('../img/5.jpg')  center (center / cover) no-repeat; }
        &_6 {
            background: url('../img/6.jpg')  center (center / cover) no-repeat; }
        &_subtitle {
            color: #ffffff;
            font-size: 24px;
            font-weight: 400;
            line-height: 31px;
            text-align: center;
            text-transform: uppercase; }
        &_plus {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            bottom: 35px;
            left: 50%;
            transform: translate(-50%);
            width: 26px;
            height: 26px;
            background-color: #000000;
            margin: 0 auto;
            border-radius: 100%;
            cursor: pointer; } } }




